<template>
  <div class="mobileMainContainer">

    <mobile-nav-view></mobile-nav-view>


    <div class="mobileMainBodyDiv">

      <div class="mobileMainBody">

        <div class="mobileContainer">



          <div class="mobileMain">

            <div  class="mobileChatRoomTotalDiv">


              <div style="background-color: #409EFF; height:20px"></div>

              <div style="font-size: 3rem; margin-left: 4vw; margin-top:0.7vh">
                <strong>聊天室</strong>
              </div>




              <div class="mobileHrText">

                <div class="mobileHrLeft">

                </div>

                <div class="mobileHrTextContainer" style="font-size: 1.2rem">
                  Future Game Lab
                </div>


                <div class="mobileHrRight">

                </div>
              </div>







              <div class="mobileChatRoomDisplayBody">




                <div style="font-size: 2rem; height:3%; width: 100%;background-color: white">
                  <strong>ronghejumiao的聊天室</strong>
                </div>




                <div v-if="showLoginModal" class="mobileLoginMask"></div>
                <!-- 登录窗口 -->
                <div v-if="showLoginModal" class="mobileLogin" style="display: flex; align-self: center;">
                  <div>
                    <h3>登录</h3>
                    <input type="text" placeholder="用户名" v-model="loginUsername"  style="align-self: center">
                    <el-color-picker  v-model="color" show-alpha style="align-self: center"></el-color-picker>

                    <button @click="login" style="align-self: center">登录</button>
                  </div>
                </div>







                <div class="mobileChatRoomDisplayAndInput">





                  <div class="mobileChatRoomDisplay" ref="mobileChatRoomDisplay">


                  </div>

                  <el-input placeholder="请输入内容" v-model="input" class="mobileChatRoomInput" ref="mobileInput">

                    <el-button slot="append" ref="mobileButton" @click="sendMessage">
                      <div><strong style="font-size: 1rem">发送</strong></div>
                    </el-button>

                  </el-input>


                </div>



                <div class="mobileUserListDiv">

                  <div class="mobileUserListHeader">

                    <strong style="margin-left: 2vw">当前在线用户：</strong>

                  </div>

                  <div class="mobileUserList" ref="mobileUserList">



                  </div>

                </div>

              </div>






            </div>

            <div class="mobileExtender"></div>

          </div>

        </div>



      </div>



    </div>




  </div>
</template>

<script scoped>
import mobileNavView from "@/views/mobile/mobileNavView";


const SYSTEM_OC = 0;// eslint-disable-line no-unused-vars
const SYSTEM_ERROR = 1;// eslint-disable-line no-unused-vars
const USER = 2;// eslint-disable-line no-unused-vars
const CLIENT_REQUEST = 3;// eslint-disable-line no-unused-vars




export default {

  beforeRouteLeave(to, from, next) {
    // 检查WebSocket连接是否已建立
    if (this.socket && this.socket.readyState === WebSocket.OPEN) {
      // 关闭WebSocket连接
      this.socket.close();
      // 可以在这里添加其他清理逻辑，比如重置状态等
      this.socket = null; // 将socket设置为null，表示连接已关闭
    }
    // 确保调用next()来解析守卫
    next();
  },






  data() {
    return {

      name: "mainVue",

      bgImageSrc:require('@/assets/pictures/background1.png'),
      bg_aboutLab:require('@/assets/pictures/bg_aboutLab.jpg'),

      input:'',

      loginUsername: '',//用户自己起的名字
      showLoginModal: true, // 控制登录窗口的显示

      color: 'rgba(0, 0, 255, 0.8)',//用户名字的颜色


      socket:null,
      connectedToServer:false,


    };
  },

  components:{

    mobileNavView

  },


  mounted(){

    this.socket = new WebSocket("ws://localhost:3000");


    // 可以通过 this.$refs 访问 DOM 元素
    this.div = this.$refs.mobileChatRoomDisplay;
    this.inputBar = this.$refs.mobileInput.$el.querySelector('input'); // 如果需要访问 input 元素
    // this.inputBar = this.$refs.pcInput;
    this.button = this.$refs.mobileButton;
    this.users = this.$refs.mobileUserList;

    document.addEventListener('keydown', this.handleKeyDown);
    this.scrollToBottom()//滚动条在最底部




    this.socket.addEventListener('message', (msg) => {

      msg = JSON.parse(msg.data)
      console.log("客户端接收到数据",msg);

      if(msg.type === USER){//聊天数据



        var texts = document.createElement('div');
        texts.classList.add('mobileChatTextDiv')


        var mobileChatHeader = document.createElement('div')
        mobileChatHeader.classList.add('mobileChatTextHeader')
        mobileChatHeader.innerText = msg.name + ":"
        mobileChatHeader.style.color = this.color

        var mobileChatTextContent = document.createElement('div')
        mobileChatTextContent.classList.add('mobileChatTextContent')
        mobileChatTextContent.innerText = msg.data

        var mobileChatTime = document.createElement('div')
        mobileChatTime.classList.add('mobileChatTime')
        mobileChatTime.innerText = msg.time

        texts.appendChild(mobileChatHeader)
        texts.appendChild(mobileChatTextContent)
        texts.appendChild(mobileChatTime)


        this.div.appendChild(texts);

        this.scrollToBottom();


      }else if(msg.type === CLIENT_REQUEST){

        if(msg.data === "username_exist"){

          alert("用户名重复，请重新输入")
          this.showLoginModal = true;
          this.connectedToServer = false;


        }
      }else if(msg.type === SYSTEM_OC){

        this.connectedToServer = true;

        var texts2 = document.createElement('div');
        texts2.classList.add('mobileChatEnterHint')
        texts2.innerText = msg.time +"    " +msg.data

        this.div.appendChild(texts2);

        this.scrollToBottom();







        console.log(msg)
        console.log(msg.userList)

        this.users.innerHTML = ''

        Array.from(msg.userList).forEach(user => {

          var texts3 = document.createElement('div')
          texts3.classList.add('mobileUserListEach')
          texts3.innerText = user

          this.users.appendChild(texts3)

        })






      }


    })




    this.socket.addEventListener('open', () => {
      console.log("客户端连接构建成功");

    });





  },


  methods:{

    sendMessage() {


      const value = this.inputBar.value;
      this.inputBar.value = '';


      console.log(value);
      // 这里可以根据需要处理发送消息的逻辑


      this.socket.send(JSON.stringify({

        type:USER,
        data:value,
        name:this.loginUsername

      }))
    },


    //按enter发送消息
    handleKeyDown(event) {


      // 检查是否按下了Enter键（keyCode 13 或 key 'Enter'）
      if ( (event.keyCode === 13 || event.key === 'Enter') && this.connectedToServer == true){
        this.sendMessage(); // 调用点击事件的处理函数
        // 阻止默认行为（可选，根据实际需求决定是否添加）
        // event.preventDefault();
      }
    },


    //滚动条保持在最底部
    scrollToBottom() {
      // 使用ref获取chatContainer的DOM元素

      // 滚动到容器的底部
      this.div.scrollTop = this.div.scrollHeight;

      // 如果在消息列表更新后也想滚动到底部，可以在更新消息的方法中也调用这个方法
    },




    login() {

      if (this.loginUsername == null || this.loginUsername == '') {
        alert('用户名不能为空！');
        return;
      }


      this.socket.send(JSON.stringify({

        type:CLIENT_REQUEST,
        data:this.loginUsername,
        time:new Date().toLocaleTimeString()


      }))

      //关闭登录界面
      this.showLoginModal = false;


    },





  }
}
</script>

<style>


.mobileMainContainer{
  height:175vh;

  display: flex;
  flex-direction: column;

}







.mobileMainBodyDiv {




  /* 设置背景图片 */
  background-image: url('../../assets/pictures/background2.png'); /* 替换为你的图片URL */

  /* 确保背景图片覆盖整个div */
  background-size: cover; /* 图片会被缩放以完全覆盖容器区域，可能会被裁剪 */

  /* 不让背景图片重复 */
  background-repeat: no-repeat;

  /* 背景图片的位置 */
  background-position: center; /* 图片居中显示 */



  /* 其他样式设置，确保div有足够的空间显示背景 */
  width: 100%; /* 或者具体宽度 */
  height:100%;

  flex:1;

  /* 为了让背景图片不随div的内容滚动而滚动，可以添加 */
  background-attachment: fixed; /* 可选，根据需求决定是否需要 */
}


.mobileMainBody {
  height: calc(100% - 10%); /* 减去 padding-top 的值 */
  padding-top: 10%;
  margin-left: 8%;
  margin-right: 8%;
  box-sizing: border-box; /* 确保 padding 和 border 不会增加元素的总宽度和高度 */
}

.mobileContainer{

  height:100%;

  display:flex;

}





.mobileMain{
  flex:1;

  display: flex;
  flex-direction: column;


  padding-top: 20px;

}


.mobileHrText{

  display: flex;
  align-items: center; /* 垂直居中 */
  text-align: center; /* 文本居中（虽然在这里flex布局下不是必需的，但有助于理解） */
  position: relative; /* 为了伪元素定位 */

}

.mobileHrLeft{

  width:50%;

  display: flex;
  align-items: center; /* 垂直居中 */
  text-align: center; /* 文本居中（虽然在这里flex布局下不是必需的，但有助于理解） */
  position: relative; /* 为了伪元素定位 */

}

.mobileHrRight{

  width:15%;

  display: flex;
  align-items: center; /* 垂直居中 */
  text-align: center; /* 文本居中（虽然在这里flex布局下不是必需的，但有助于理解） */
  position: relative; /* 为了伪元素定位 */

}

.mobileHrTextContainer{

  color: #000; /* 文字颜色 */
  font-size: 16px; /* 文字大小 */



}
.mobileHrLeft::before,
.mobileHrRight::after{

  content: ''; /* 伪元素内容为空 */
  flex: 1; /* 使得伪元素占据剩余空间 */
  height: 1px; /* 分割线高度 */
  background-color: #015bcb; /* 分割线颜色 */
  margin: 0 8px; /* 分割线与文字的间距 */

}






.mobileChatRoomTotalDiv{

  height: 100%;

  background-color: rgba(255,255,255,0.5);
  display: flex;
  flex-direction: column;

}

.mobileChatRoomDisplayBody{



  flex: 1;

  display: flex;
  flex-direction: column;

  margin-top:1vh;
  margin-left: 3vw;
  margin-right:3vw;

  position: relative;
}

.mobileChatRoomDisplayAndInput{

  overflow: auto;

  flex: 1;
  display: flex;
  flex-direction: column;

}


.mobileChatRoomDisplay{


  overflow: auto;

  flex:1;

  background-color: #e7e7e7;


}

.mobileUserListDiv{



  flex:1;
  display: flex;
  flex-direction: column;

  background-color: #f1f1f1;

}

.mobileUserListHeader{


  font-size: 1.5rem;

  width:100%;
  flex:1;

  background-color: white;
}

.mobileUserList{

  height:95%;


}






.mobileExtender{

  height:5%;
  background-color: rgba(255,255,255,0.5);
}













.mobileLoginMask {

  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色遮罩 */
  display: flex;
  align-items: center;
  justify-content: center;

  z-index: 2000;
}

.mobileLogin{

  position: absolute;

  padding: 2%;
  background-color: #fff;

  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 3000; /* 确保登录窗口在遮罩层之上 */

  align-self: center;
  transform: translate(0, 100%);

  width: 70%;
  height:12vh;


}



.mobileUserList{

  overflow: auto;


}


.mobileChatRoomDisplay{

  overflow: auto;

}



</style>