<template>
  <div class="mainContainer">

    <nav-view/>


    <div class="mainBodyDiv">

      <div class="mainBody">

        <div class="container">

          <div class="aside" style="width:25%">

            <el-aside style="width:100%" >


              <el-row class="tac" >


                <el-col style="overflow: hidden">

                  <el-menu
                      default-active="1"
                      class="pcMenu"
                  >

                    <el-submenu index="1" class="pcSubmenu">
                      <template slot="title">
                        <i class="el-icon-location"></i>
                        <span style="font-size: 1rem">关于Lab</span>
                      </template>

                      <el-menu-item-group>
                        <el-menu-item index="1-1" class="pcMenuItem">

                          <router-link to="/about" class="pcMenuLink">关于Lab</router-link>

                        </el-menu-item>
                        <el-menu-item index="1-2" class="pcMenuItem">

                          <router-link to="/project" class="pcMenuLink">项目</router-link>

                        </el-menu-item>



                        <el-menu-item index="1-3" class="pcMenuItem">

                          <router-link to="/member" class="pcMenuLink">成员</router-link>

                        </el-menu-item>

                      </el-menu-item-group>

                    </el-submenu>
                    <el-menu-item index="2" class="pcMenuItem">
                      <i class="el-icon-menu"></i>

                      <router-link to="/chatRoom" class="pcMenuLink">聊天室</router-link>

                    </el-menu-item>


                  </el-menu>
                </el-col>
              </el-row>

            </el-aside>

            <div class="extender">

            </div>

          </div>




          <div class="main">

            <div style="background-color: rgba(255,255,255,0.5)">


              <div style="background-color: #409EFF; height:20px"></div>



              <div style="
                          padding-top: 20px;
                          padding-left:20px;

                          ">

                <h2>聊天室</h2>

                <div class="hrText">

                  <div class="hrLeft">

                  </div>

                  <div class="hrTextContainer">
                    Future Game Lab
                  </div>


                  <div class="hrRight">

                  </div>
                </div>
              </div>
            </div>





            <div style="background-color: rgba(255,255,255,0.5); display: flex; flex-direction: column;height:55% ">




              <div class="pcChatRoomBody">




                <div v-if="showLoginModal" class="pcLoginMask"></div>
                <!-- 登录窗口 -->
                <div v-if="showLoginModal" class="pcLogin" style="display: flex; align-self: center;">
                  <div>
                    <h3>登录</h3>
                    <input type="text" placeholder="用户名" v-model="loginUsername"  style="align-self: center">
                    <el-color-picker  v-model="color" show-alpha style="align-self: center"></el-color-picker>

                    <button @click="login" style="align-self: center">登录</button>
                  </div>
                </div>




                <div class="pcUserListDiv">

                  <div class="pcUserListHeader">

                    <strong>当前在线用户:</strong>

                  </div>

                  <div class="pcUserList" ref="pcUserList">


                  </div>


                </div>

                <div class="pcChatRoomDisplayChatBody">

                  <div style="height: 5%">
                    <strong>ronghejumiao的聊天室</strong>
                  </div>


                  <div class="pcChatRoomDisplay" ref="pcChatRoomDisplay">


                  </div>

                  <el-input placeholder="请输入内容" v-model="input" class="input-with-select" ref="pcInput">


                    <el-button slot="append" @click="sendMessage" ref="pcButton">
                      <div><strong style="font-size: 1rem">发送</strong></div>
                    </el-button>

                  </el-input>


                </div>


              </div>



            </div>


            <div class="pcExtenderForChatRoom"></div>

          </div>

        </div>



      </div>



    </div>




  </div>
</template>

<script>

const SYSTEM_OC = 0;// eslint-disable-line no-unused-vars
const SYSTEM_ERROR = 1;// eslint-disable-line no-unused-vars
const USER = 2;// eslint-disable-line no-unused-vars
const CLIENT_REQUEST = 3;// eslint-disable-line no-unused-vars



import navView  from "@/views/pc/navView";


export default {
  name: "chatRoom",

  beforeRouteLeave(to, from, next) {
    // 检查WebSocket连接是否已建立
    if (this.socket && this.socket.readyState === WebSocket.OPEN) {
      // 关闭WebSocket连接
      this.socket.close();
      // 可以在这里添加其他清理逻辑，比如重置状态等
      this.socket = null; // 将socket设置为null，表示连接已关闭
    }
    // 确保调用next()来解析守卫
    next();
  },

  data(){

    return{

      input:'',

      loginUsername: '',//用户自己起的名字
      showLoginModal: true, // 控制登录窗口的显示

      color: 'rgba(0, 0, 255, 0.8)',//用户名字的颜色


      socket:null,
      connectedToServer: false
    }


  },


  components:{

    navView


  },



  mounted() {
    this.socket = new WebSocket("ws://localhost:3000");
//?username=${encodeURIComponent(this.loginUsername)}




    // 可以通过 this.$refs 访问 DOM 元素
    this.div = this.$refs.pcChatRoomDisplay;
    this.inputBar = this.$refs.pcInput.$el.querySelector('input'); // 如果需要访问 input 元素
    // this.inputBar = this.$refs.pcInput;
    this.button = this.$refs.pcButton;
    this.users = this.$refs.pcUserList;



    document.addEventListener('keydown', this.handleKeyDown);
    this.scrollToBottom()//滚动条在最底部




    this.socket.addEventListener('message', (msg) => {

        msg = JSON.parse(msg.data)
      console.log("客户端接收到数据",msg);

      if(msg.type === USER){//聊天数据



        var texts = document.createElement('div');
        texts.classList.add('pcChatTextDiv')


        var pcChatHeader = document.createElement('div')
        pcChatHeader.classList.add('pcChatTextHeader')
        pcChatHeader.innerText = msg.name + ":"
        pcChatHeader.style.color = this.color

        var pcChatTextContent = document.createElement('div')
        pcChatTextContent.classList.add('pcChatTextContent')
        pcChatTextContent.innerText = msg.data

        var pcChatTime = document.createElement('div')
        pcChatTime.classList.add('pcChatTime')
        pcChatTime.innerText = msg.time

        texts.appendChild(pcChatHeader)
        texts.appendChild(pcChatTextContent)
        texts.appendChild(pcChatTime)


        this.div.appendChild(texts);

        this.scrollToBottom();


      }else if(msg.type === CLIENT_REQUEST){

        if(msg.data === "username_exist"){

          alert("用户名重复，请重新输入")
          this.showLoginModal = true;
          this.connectedToServer = false;


        }
      }else if(msg.type === SYSTEM_OC){

        this.connectedToServer = true;

        var texts2 = document.createElement('div');
        texts2.classList.add('pcChatEnterHint')
        texts2.innerText = msg.time +"    " +msg.data

        this.div.appendChild(texts2);

        this.scrollToBottom();







        console.log(msg)
        console.log(msg.userList)

        this.users.innerHTML = ''

        Array.from(msg.userList).forEach(user => {

          var texts3 = document.createElement('div')
          texts3.classList.add('pcUserListEach')
          texts3.innerText = user

          this.users.appendChild(texts3)

        })






      }


    })








    this.socket.addEventListener('open', () => {
      console.log("客户端连接构建成功");

    });








  },




  methods: {
    sendMessage() {


      const value = this.inputBar.value;
      this.inputBar.value = '';


      console.log(value);
      // 这里可以根据需要处理发送消息的逻辑


      this.socket.send(JSON.stringify({

        type:USER,
        data:value,
        name:this.loginUsername

      }))
    },



    //按enter发送消息
    handleKeyDown(event) {


      // 检查是否按下了Enter键（keyCode 13 或 key 'Enter'）
      if ( (event.keyCode === 13 || event.key === 'Enter') && this.connectedToServer == true){
        this.sendMessage(); // 调用点击事件的处理函数
        // 阻止默认行为（可选，根据实际需求决定是否添加）
        // event.preventDefault();
      }
    },


    //滚动条保持在最底部
    scrollToBottom() {
      // 使用ref获取chatContainer的DOM元素

      // 滚动到容器的底部
      this.div.scrollTop = this.div.scrollHeight;

      // 如果在消息列表更新后也想滚动到底部，可以在更新消息的方法中也调用这个方法
    },


    login() {

      if (this.loginUsername == null || this.loginUsername == '') {
        alert('用户名不能为空！');
        return;
      }


      this.socket.send(JSON.stringify({

        type:CLIENT_REQUEST,
        data:this.loginUsername,
        time:new Date().toLocaleTimeString()


      }))

      //关闭登录界面
      this.showLoginModal = false;


    },



  }



}
</script>



<style  src="../../../src/assets/css/pcChatRoomCss.css" scoped>







</style>